<template>
  <div class="html-editer">
    <editor
      :content="content"
      :height="editHeight"
      :z-index="1000"
      :show-module-name="showModuleName"
      ref="editor"
      @change="updateData"></editor>
  </div>
</template>

<script>
  import VueHtml5Editor from 'vue-html5-editor'

  const editor = new VueHtml5Editor({
    showModuleName: true,
    image: {
      sizeLimit: 512 * 1024,
      compress: true,
      width: 500,
      height: 500,
      quality: 80
    },
    visibleModules: [
      'text',
      'color',
      'font',
      'align',
      'list',
      'link',
      'unlink',
      'tabulation',
      'hr',
      'eraser',
      'undo',
      'full-screen',
      'image'
    ],
    language: 'zh-cn',
    i18n: {
      'zh-cn': {
        'align': '',
        'image': '',
        'list': '',
        'link': '',
        'unlink': '',
        'table': '',
        'font': '',
        'full screen': '',
        'text': '',
        'eraser': '',
        'info': '',
        'color': '',
        'please enter a url': '',
        'create link': '',
        'bold': '',
        'italic': '',
        'underline': '',
        'strike through': '',
        'subscript': '',
        'superscript': '',
        'heading': '',
        'font name': '',
        'font size': '',
        'left justify': '',
        'center justify': '',
        'right justify': '',
        'ordered list': '',
        'unordered list': '',
        'fore color': '',
        'background color': '',
        'row count': '',
        'column count': '',
        'save': '',
        'upload': '',
        'progress': '',
        'unknown': '',
        'please wait': '',
        'error': '',
        'abort': '',
        'reset': ''
      }
    }
  })

  export default {
    name: 've-html5-editer',
    components: {editor},
    data () {
      return {
        showModuleName: false,
        content: ''
      }
    },
    props: {
      value: {
        default: ''
      },
      height: {
        type: String,
        default: '300'
      },
      imgUploadUrl: {
        type: String,
        default: '/api/upload/do-upload'
      }
    },
    computed: {
      editHeight () {
        return this.height.indexOf('%') !== -1 ? this.height : (this.height - 21)
      }
    },
    watch: {
      value: {
        handler (newVal) {
          this.content = newVal
        },
        immediate: true
      }
    },
    methods: {
      updateData (res) {
        this.$emit('input', res)
        this.$emit('change', res)
      }
    }
  }
</script>

<style lang="scss">
  .html-editer {
    min-width: 480px;
    .content{
      max-height: 600px;
    }
  }
</style>
