<template>
  <div class="edit-step-box">
    <div class="edit-content clearfix">
      <div class="edit-content-box fl">
        <ve-editer
          height="500"
          v-model="email.content"></ve-editer>
        <div style="width: 50%;margin: 0 auto;padding: 20px 0;">
          <div></div>
          <div>
            <input v-model="testEmail" placeholder=""/>
            <el-button class="live-btn" type="primary" plain @click="sendTestEmail"></el-button>
          </div>
        </div>
      </div>
      <div class="edit-content-temp fr">
        <div class="temp-title">
          
          <el-button class="live-btn fr" type="primary" plain @click="recoverDefault"></el-button>
        </div>
        <div class="temp-boxs">
          <div v-for="(emailItem,idx) in emailList"
               :class="{'temp-item':true,fl:true,active:emailItem.emailTemplateId==email.emailTemplateId}"
               @click.stop="changeTemp(idx)">
            {{emailItem.title}}
          </div>
        </div>
      </div>
    </div>
    <div class="step-btns">
      <el-button class="live-btn fl" type="primary" plain @click="goLiveManger"></el-button>
      <el-button class="live-btn fr" type="primary" plain @click="nextEmail"></el-button>
      <el-button class="live-btn fr" type="primary" plain @click="saveEmail"></el-button>
    </div>
    <div ref="emailBox">
      <table class="email-box">
        <tr>
          <td>dddd</td>
          <td>dddd</td>
          <td>dddd</td>
        </tr>
        <tr>
          <td>dddd</td>
          <td>dddd</td>
          <td>dddd</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
  import LiveHttp from 'src/api/activity-manger'
  // import VeEditer from 'src/components/ve-editer'
  import VeEditer from 'src/components/ve-html5-editer'
  // import editStepTwo from './edit-step-two'
  import {mapState, mapMutations} from 'vuex'
  import * as types from '../../../store/mutation-types'

  export default {
    name: 'edit-step-one',
    data () {
      return {
        testEmail: '',
        emailList: [],
        email: {
          activityId: '',
          emailInviteId: '',
          emailTemplateId: 1,
          title: '',
          content: '',
          desc: '',
          senderName: ''
        }
      }
    },
    components: {VeEditer},
    computed: mapState('liveMager', {
      emailInfo: state => state.emailInfo
    }),
    watch: {
      emailInfo: {
        handler (newVal) {
          this.email = {...this.email, ...newVal}
        },
        immediate: true
      }
    },
    created () {
      // vuex
      const queryId = this.$route.params.id
      if (!queryId) {
        this.$router.go(-1)
        return
      }
      const emailId = this.$route.query.email
      if (emailId) { // 
        // vuexreturn
        if (this.email.emailInviteId) {
          this.queryEmailTemp()
          return
        }
        this.email.emailInviteId = emailId
        this.queryEmailInfo()
      } else { // 
        this.email = {
          activityId: queryId,
          emailInviteId: '',
          emailTemplateId: 1,
          title: '',
          content: '',
          desc: '',
          senderName: ''
        }
        this.storeEmailInfo(this.email)
      }
      this.queryEmailTemp()
    },
    methods: {
      ...mapMutations('liveMager', {
        storeEmailInfo: types.EMAIL_INFO
      }),
      /*  */
      queryEmailInfo () {
        // return
        if (!this.email.emailInviteId) return
        LiveHttp.queryEmailInfoById(this.email.emailInviteId).then((res) => {
          this.email = res.data
          this.storeEmailInfo(this.email)
        })
      },
      queryEmailTemp () {
        LiveHttp.queryEmailTemplateList().then((res) => {
          this.emailList = res.data.list
          if (!this.email.emailInviteId) { // 
            this.email.content = this.emailList[0].content
          }
        })
      },
      sendTestEmail () {
        if (!this.testEmail) {
          this.$messageBox({
            header: '',
            content: '',
            confirmText: '',
            autoClose: 10
          })
          return
        }
        if (!this.email.content) {
          this.$messageBox({
            header: '',
            content: '',
            confirmText: '',
            autoClose: 10
          })
          return
        }
        LiveHttp.sendTestEmailInfo({
          content: this.email.content,
          receiverEmail: this.testEmail
        }).then((res) => {
          if (res.code === 200) {
            this.$toast({
              header: ``,
              content: '',
              autoClose: 2000,
              position: 'right-top'
            })
          }
        })
      },
      /*  */
      saveEmail () {
        LiveHttp.saveEmailInfo(this.email).then((res) => {
          // id
          this.email.emailInviteId = res.data.emailInviteId
          this.email.title = res.data.title
          // vuex
          this.storeEmailInfo(this.email)
          this.$toast({
            header: ``,
            content: '',
            autoClose: 2000,
            position: 'right-top'
          })
        })
      },
      nextEmail () {
        this.storeEmailInfo(this.email)
        // 
        this.$emit('changeView', 1)
      },
      /*  */
      changeTemp (idx) {
        this.$messageBox({
          header: '',
          content: '',
          cancelText: '',
          confirmText: '',
          handleClick: (e) => {
            if (e.action === 'confirm') {
              this.email.emailTemplateId = this.emailList[idx].emailTemplateId
              this.email.content = this.emailList[idx].content
            }
          }
        })
      },
      /*  */
      recoverDefault () {
        this.$messageBox({
          header: '',
          content: '',
          cancelText: '',
          confirmText: '',
          handleClick: (e) => {
            if (e.action === 'confirm') {
              for (let i = 0; i < this.emailList.length; i++) {
                const emailObj = this.emailList[i]
                if (emailObj.emailTemplateId === this.email.emailTemplateId) {
                  this.email.content = emailObj.content
                  break
                }
              }
            }
          }
        })
      },
      /*  */
      goLiveManger () {
        this.$router.push(`/liveMager/email/${this.email.activityId}`)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .edit-step-box {
    height: 800px;
    .edit-content {
      margin: 20px 0;
      .edit-content-temp {
        width: 400px;
        margin-top: 20px;
        .temp-title {
          margin: 0 20px;
        }
        .temp-boxs {
          margin: 20px;
          .temp-item {
            width: 160px;
            height: 180px;
            line-height: 180px;
            margin: 10px;
            text-align: center;
            box-sizing: border-box;
            border: solid 1px #e5e5e5;
          }
          .active {
            border-color: red;
          }
        }
      }
      .edit-content-box {
        width: calc(100% - 400px);
      }
    }
    .step-btns {
      margin-top: 20px;
    }
  }
</style>
